:root {
    --primary: #0070f3;
    --secondary: #0070f3;
    --background: #fff;
    --text: #333;
    --error: #ff4d4f;
    --border: #eaeaea;
  }
  
  * {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
  }
  
  html,
  body {
    font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen,
      Ubuntu, Cantarell, Fira Sans, Droid Sans, Helvetica Neue, sans-serif;
    background-color: var(--background);
    color: var(--text);
  }
  
  .container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 2rem;
  }
  
  header {
    margin-bottom: 2rem;
    text-align: center;
  }
  
  main {
    display: grid;
    grid-template-columns: 2fr 1fr;
    gap: 2rem;
  }
  
  @media (max-width: 768px) {
    main {
      grid-template-columns: 1fr;
    }
  }
  
  .grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
    gap: 1rem;
    margin-top: 1rem;
  }
  
  .color-card {
    border: 1px solid var(--border);
    border-radius: 8px;
    overflow: hidden;
  }
  
  .color-sample {
    height: 100px;
    width: 100%;
  }
  
  .color-details {
    padding: 1rem;
  }
  
  .color-details h3 {
    margin-bottom: 0.5rem;
  }
  
  .color-details .date {
    font-size: 0.8rem;
    color: #666;
    margin-top: 0.5rem;
  }
  
  .color-form {
    padding: 1.5rem;
    border: 1px solid var(--border);
    border-radius: 8px;
  }
  
  .form-group {
    margin-bottom: 1rem;
  }
  
  label {
    display: block;
    margin-bottom: 0.5rem;
    font-weight: 500;
  }
  
  input[type="text"] {
    width: 100%;
    padding: 0.75rem;
    border: 1px solid var(--border);
    border-radius: 4px;
    font-size: 1rem;
  }
  
  .color-picker {
    display: flex;
    align-items: center;
    gap: 1rem;
  }
  
  input[type="color"] {
    width: 50px;
    height: 40px;
    border: 1px solid var(--border);
    border-radius: 4px;
  }
  
  button {
    background-color: var(--primary);
    color: white;
    border: none;
    border-radius: 4px;
    padding: 0.75rem 1.5rem;
    font-size: 1rem;
    cursor: pointer;
    transition: opacity 0.2s;
  }
  
  button:hover {
    opacity: 0.9;
  }
  
  button:disabled {
    opacity: 0.6;
    cursor: not-allowed;
  }
  
  .error {
    color: var(--error);
    margin: 0.5rem 0;
  }
  
  footer {
    margin-top: 3rem;
    text-align: center;
    color: #666;
    font-size: 0.9rem;
  }